@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
body {
  margin: 0;
  background: #65dad0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: "Open Sans", sans-serif;
}

#root {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.App{
  width: 30rem;
  max-width: 90vw;
  height:40rem;
  max-height: 90vh;
  margin: 1rem;
  background: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0px 1px 2px 0px rgba(50, 50, 50, 0.2);
  color: #263238;
  display: flex;
  flex-direction: column;
}

.App h1{
  margin: 0;
  margin-bottom: 1.5 rem;
  font-weight: 200;
  font-size: 3rem;
  position: relative;
  color: #00897b;
  text-align: center;
}

.ItemList{
  margin: 1rem 0;
  list-style-type: none;
  padding: 0;
  overflow: scroll;
}

.Item {
  font-size: 1.2rem;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #eceff1;
  cursor: pointer;
}

.Item.completed {
  color: #90a4ae;
  text-decoration: line-through;
}

.ItemForm input {
  width: 100%;
  font-size: 1.2rem;
  padding: 0.5rem 0.5rem;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #90a4ae;
}

.ItemForm input:focus {
  border-bottom: 1px solid #00897b;
  outline: 0;
}

.ItemForm input:focus::-webkit-input-placeholder {
  color: #00897b;
}

.ItemForm input:focus::-moz-placeholder {
  color: #00897b;
}

.ItemForm input:focus:-ms-input-placeholder {
  color: #00897b;
}

.ItemForm input:focus::-ms-input-placeholder {
  color: #00897b;
}

.ItemForm input:focus::placeholder {
  color: #00897b;
}

.ItemForm input::-webkit-input-placeholder {
  color: #90a4ae;
  font-weight: 100;
}

.ItemForm input::-moz-placeholder {
  color: #90a4ae;
  font-weight: 100;
}

.ItemForm input:-ms-input-placeholder {
  color: #90a4ae;
  font-weight: 100;
}

.ItemForm input::-ms-input-placeholder {
  color: #90a4ae;
  font-weight: 100;
}

.ItemForm input::placeholder {
  color: #90a4ae;
  font-weight: 100;
}


